"use client";

import { useEffect, useState, ChangeEvent, FormEvent } from "react";
import { useRouter, useParams } from "next/navigation";
import { doc, setDoc } from "firebase/firestore";
import { db } from "../../../firebaseConfig";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

type BoardImage = { name: string; src: string }; // src„ÅØFirebase Storage„ÅÆURL
type PracticeRecord = {
  lessonId: string;
  practiceDate: string;
  reflection: string;
  boardImages: BoardImage[];
  lessonTitle: string;
};
type LessonPlan = {
  id: string;
  result?: string | object;
};

export default function PracticeAddPage() {
  const router = useRouter();
  const { id } = useParams() as { id: string };

  const [practiceDate, setPracticeDate] = useState("");
  const [reflection, setReflection] = useState("");
  const [boardImages, setBoardImages] = useState<BoardImage[]>([]);
  const [lessonTitle, setLessonTitle] = useState("");
  const [record, setRecord] = useState<PracticeRecord | null>(null);
  const [lessonPlan, setLessonPlan] = useState<LessonPlan | null>(null);
  const [uploading, setUploading] = useState(false);

  const storage = getStorage();

  useEffect(() => {
    const plansJson = localStorage.getItem("lessonPlans") || "[]";
    let plans: LessonPlan[];
    try {
      plans = JSON.parse(plansJson) as LessonPlan[];
    } catch {
      plans = [];
    }
    const plan = plans.find((p) => p.id === id) || null;
    setLessonPlan(plan);

    if (plan && plan.result) {
      if (typeof plan.result === "string") {
        const firstLine = plan.result.split("\n")[0].replace(/^„ÄêÂçòÂÖÉÂêç„Äë\s*/, "");
        setLessonTitle(firstLine);
      } else if (typeof plan.result === "object") {
        const unitName = (plan.result as any)["ÂçòÂÖÉÂêç"];
        setLessonTitle(typeof unitName === "string" ? unitName : "");
      } else {
        setLessonTitle("");
      }
    } else {
      setLessonTitle("");
    }

    const recsJson = localStorage.getItem("practiceRecords") || "[]";
    let recs: PracticeRecord[];
    try {
      recs = JSON.parse(recsJson) as PracticeRecord[];
    } catch {
      recs = [];
    }
    const existing = recs.find((r) => r.lessonId === id) || null;
    if (existing) {
      setPracticeDate(existing.practiceDate);
      setReflection(existing.reflection);
      setBoardImages(existing.boardImages);
      setRecord({ ...existing, lessonTitle: existing.lessonTitle || "" });
    }
  }, [id]);

  const handleFileChange = async (e: ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return;
    setUploading(true);
    const files = Array.from(e.target.files);
    const uploadedImages: BoardImage[] = [];
    try {
      for (const file of files) {
        const storageRef = ref(storage, `practiceImages/${id}/${Date.now()}_${file.name}`);
        const snapshot = await uploadBytes(storageRef, file);
        const url = await getDownloadURL(snapshot.ref);
        uploadedImages.push({ name: file.name, src: url });
      }
      setBoardImages((prev) => [...prev, ...uploadedImages]);
    } catch (error) {
      alert("ÁîªÂÉè„ÅÆ„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(error);
    } finally {
      setUploading(false);
      e.target.value = "";
    }
  };

  const handleRemoveImage = (i: number) =>
    setBoardImages((prev) => prev.filter((_, idx) => idx !== i));

  const handlePreview = (e: FormEvent) => {
    e.preventDefault();
    setRecord({
      lessonId: id,
      practiceDate,
      reflection,
      boardImages,
      lessonTitle,
    });
  };

  const handlePdfDownload = async () => {
    if (!record || !lessonPlan) {
      alert("„Éó„É¨„Éì„É•„Éº„Çí‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }
    const { default: html2pdf } = await import("html2pdf.js");
    const el = document.getElementById("practice-preview");
    if (!el) return;
    await html2pdf()
      .from(el)
      .set({
        margin: 10,
        filename: `${lessonTitle}_ÂÆüË∑µË®òÈå≤.pdf`,
        jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
        html2canvas: { scale: 2 },
        pagebreak: { mode: ["avoid-all"] },
      })
      .save();
  };

  const handleSaveAll = async () => {
    if (!record || !lessonPlan) {
      alert("„Éó„É¨„Éì„É•„Éº„Çí‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }

    // localStorage‰øùÂ≠òÔºàÁîªÂÉè„ÅØURL„Å™„ÅÆ„ÅßÂÆπÈáèÂïèÈ°å„Å™„ÅóÔºâ
    const recsJson = localStorage.getItem("practiceRecords") || "[]";
    let recs: PracticeRecord[];
    try {
      recs = JSON.parse(recsJson) as PracticeRecord[];
    } catch {
      recs = [];
    }
    const idx = recs.findIndex((r) => r.lessonId === id);
    if (idx >= 0) recs[idx] = record;
    else recs.push(record);

    try {
      localStorage.setItem("practiceRecords", JSON.stringify(recs));
    } catch (e) {
      alert("localStorage„Å∏„ÅÆ‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇÂÆπÈáè„Ç™„Éº„Éê„Éº„Åã„ÇÇ„Åó„Çå„Åæ„Åõ„Çì„ÄÇ");
      console.error(e);
      return;
    }

    // Firestore„Å´‰øùÂ≠ò
    try {
      await setDoc(doc(db, "practice_records", id), record);
    } catch (e) {
      alert("Firestore„Å∏„ÅÆ‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(e);
      return;
    }

    // PDFÁîüÊàê
    const { default: html2pdf } = await import("html2pdf.js");
    const el = document.getElementById("practice-preview");
    if (!el) {
      alert("„Éó„É¨„Éì„É•„Éº„Ç®„É™„Ç¢„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„ÄÇ");
      return;
    }
    let pdfBlob: Blob;
    try {
      pdfBlob = await html2pdf()
        .from(el)
        .set({
          margin: 10,
          jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
          html2canvas: { scale: 2 },
          pagebreak: { mode: ["avoid-all"] },
        })
        .outputPdf("blob");
    } catch (e) {
      alert("PDFÁîüÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(e);
      return;
    }

    // Drive„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ
    try {
      const { uploadToDrive } = await import("../../../../lib/drive");
      const folderId = process.env.NEXT_PUBLIC_GOOGLE_DRIVE_FOLDER_ID;
      if (!folderId) {
        alert("Drive„Éï„Ç©„É´„ÉÄID„ÅåÊú™Ë®≠ÂÆö„Åß„Åô");
        return;
      }
      await uploadToDrive(pdfBlob, `${lessonTitle}_ÂÆüË∑µË®òÈå≤.pdf`, "application/pdf", folderId);
    } catch (e) {
      alert("Drive„Å∏„ÅÆ„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(e);
      return;
    }

    alert("‰øùÂ≠òÔºãPDFÁîüÊàêÔºãDrive‰øùÂ≠ò„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ");
    router.push("/practice/history");
  };

  const navBtn: React.CSSProperties = {
    padding: "8px 12px",
    backgroundColor: "#1976d2",
    color: "#fff",
    border: "none",
    borderRadius: 6,
    cursor: "pointer",
    whiteSpace: "nowrap",
    marginRight: 8,
  };
  const sectionStyle: React.CSSProperties = {
    border: "2px solid #1976d2",
    padding: 12,
    borderRadius: 6,
    marginBottom: 16,
  };
  const imgBox: React.CSSProperties = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    border: "1px solid #ccc",
    padding: 8,
    borderRadius: 6,
  };
  const rmBtn: React.CSSProperties = {
    marginTop: 4,
    padding: 4,
    backgroundColor: "#e53935",
    color: "white",
    border: "none",
    borderRadius: 4,
    cursor: "pointer",
  };
  const actionBtn: React.CSSProperties = {
    padding: 12,
    backgroundColor: "#4CAF50",
    color: "#fff",
    border: "none",
    borderRadius: 6,
    width: "100%",
    cursor: "pointer",
    marginBottom: 8,
  };
  const pdfBtn: React.CSSProperties = { ...actionBtn, backgroundColor: "#607D8B" };

  return (
    <main style={{ padding: 24, maxWidth: 800, margin: "auto", fontFamily: "sans-serif" }}>
      <nav style={{ display: "flex", overflowX: "auto", marginBottom: 24 }}>
        <button onClick={() => router.push("/")} style={navBtn}>
          üè† „Éõ„Éº„É†
        </button>
        <button onClick={() => router.push("/plan")} style={navBtn}>
          üìã ÊéàÊ•≠‰ΩúÊàê
        </button>
        <button onClick={() => router.push("/plan/history")} style={navBtn}>
          üìñ Ë®àÁîªÂ±•Ê≠¥
        </button>
        <button onClick={() => router.push("/practice/history")} style={navBtn}>
          üì∑ ÂÆüË∑µÂ±•Ê≠¥
        </button>
        <button onClick={() => router.push("/models/create")} style={navBtn}>
          ‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê
        </button>
        <button onClick={() => router.push("/models")} style={navBtn}>
          üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß
        </button>
        <button onClick={() => router.push("/models")} style={navBtn}>
          üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥
        </button>
      </nav>

      <h2>ÂÆüË∑µË®òÈå≤‰ΩúÊàê„ÉªÁ∑®ÈõÜ</h2>

      <form onSubmit={handlePreview}>
        <div style={sectionStyle}>
          <label>
            ÂÆüÊñΩÊó•Ôºö
            <br />
            <input
              type="date"
              value={practiceDate}
              required
              onChange={(e) => setPracticeDate(e.target.value)}
              style={{ width: "100%", padding: 8 }}
            />
          </label>
        </div>

        <div style={sectionStyle}>
          <label>
            ÊåØ„ÇäËøî„ÇäÔºö
            <br />
            <textarea
              value={record?.reflection ?? reflection}
              required
              onChange={(e) => setReflection(e.target.value)}
              rows={6}
              style={{ width: "100%", padding: 8 }}
            />
          </label>
        </div>

        <label style={{ display: "block", marginBottom: 8, cursor: "pointer" }}>
          <span style={{ ...navBtn, width: "100%", textAlign: "center" }}>üì∑ ÊùøÊõ∏ÂÜôÁúü„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ</span>
          <input
            type="file"
            multiple
            accept="image/*"
            onChange={handleFileChange}
            style={{ display: "none" }}
          />
        </label>

        <div style={{ display: "flex", gap: 8, flexWrap: "wrap", marginBottom: 16 }}>
          {boardImages.map((img, i) => (
            <div key={img.name + i} style={imgBox}>
              <img
                src={img.src}
                alt={img.name}
                style={{ width: 160, height: 160, objectFit: "cover", borderRadius: 4 }}
              />
              <button onClick={() => handleRemoveImage(i)} style={rmBtn}>
                üóë
              </button>
            </div>
          ))}
        </div>

        <button type="submit" style={{ ...actionBtn, marginBottom: 16 }} disabled={uploading}>
          {uploading ? "„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ‰∏≠..." : "„Éó„É¨„Éì„É•„Éº„Çí‰ΩúÊàê"}
        </button>
      </form>

      {record && (
        <>
          <section
            id="practice-preview"
            style={{
              marginBottom: 16,
              padding: 24,
              border: "1px solid #ccc",
              borderRadius: 6,
              backgroundColor: "#fff",
              fontSize: 14,
              lineHeight: 1.6,
              fontFamily: "'Hiragino Kaku Gothic ProN', sans-serif",
            }}
          >
            <h2>
              {lessonPlan?.result && typeof lessonPlan.result === "object"
                ? (lessonPlan.result as any)["ÂçòÂÖÉÂêç"]
                : lessonTitle}
            </h2>

            {lessonPlan?.result && typeof lessonPlan.result === "object" && (
              <>
                <section style={{ marginBottom: 16 }}>
                  <h3>ÊéàÊ•≠„ÅÆÊ¶ÇË¶Å</h3>
                  <p>
                    <strong>ÊïôÁßëÊõ∏ÂêçÔºö</strong>
                    {(lessonPlan.result as any)["ÊïôÁßëÊõ∏Âêç"]}
                  </p>
                  <p>
                    <strong>Â≠¶Âπ¥Ôºö</strong>
                    {(lessonPlan.result as any)["Â≠¶Âπ¥"]}
                  </p>
                  <p>
                    <strong>„Ç∏„É£„É≥„É´Ôºö</strong>
                    {(lessonPlan.result as any)["„Ç∏„É£„É≥„É´"]}
                  </p>
                  <p>
                    <strong>ÊéàÊ•≠ÊôÇÈñìÊï∞Ôºö</strong>
                    {(lessonPlan.result as any)["ÊéàÊ•≠ÊôÇÈñìÊï∞"]}ÊôÇÈñì
                  </p>
                  <p>
                    <strong>ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßøÔºö</strong>
                    {(lessonPlan.result as any)["ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßø"]}
                  </p>
                </section>

                <section style={{ marginBottom: 16 }}>
                  <h3>ÂçòÂÖÉ„ÅÆÁõÆÊ®ô</h3>
                  <p>{(lessonPlan.result as any)["ÂçòÂÖÉ„ÅÆÁõÆÊ®ô"]}</p>
                </section>

                {(lessonPlan.result as any)["Ë©ï‰æ°„ÅÆË¶≥ÁÇπ"] && (
                  <section style={{ marginBottom: 16 }}>
                    <h3>Ë©ï‰æ°„ÅÆË¶≥ÁÇπ</h3>
                    {Object.entries((lessonPlan.result as any)["Ë©ï‰æ°„ÅÆË¶≥ÁÇπ"]).map(([k, v]) => (
                      <div key={k}>
                        <strong>{k}</strong>
                        <ul>
                          {(v as string[]).map((item, i) => (
                            <li key={i}>{item}</li>
                          ))}
                        </ul>
                      </div>
                    ))}
                  </section>
                )}

                {(lessonPlan.result as any)["Ë®ÄË™ûÊ¥ªÂãï„ÅÆÂ∑•Â§´"] && (
                  <section style={{ marginBottom: 16 }}>
                    <h3>Ë®ÄË™ûÊ¥ªÂãï„ÅÆÂ∑•Â§´</h3>
                    <p>{(lessonPlan.result as any)["Ë®ÄË™ûÊ¥ªÂãï„ÅÆÂ∑•Â§´"]}</p>
                  </section>
                )}

                {(lessonPlan.result as any)["ÊéàÊ•≠„ÅÆÊµÅ„Çå"] && (
                  <section style={{ marginBottom: 16 }}>
                    <h3>ÊéàÊ•≠„ÅÆÊµÅ„Çå</h3>
                    <ul>
                      {Object.entries((lessonPlan.result as any)["ÊéàÊ•≠„ÅÆÊµÅ„Çå"]).map(
                        ([key, value]) => (
                          <li key={key}>
                            <strong>{key}Ôºö</strong>
                            {value as string}
                          </li>
                        )
                      )}
                    </ul>
                  </section>
                )}
              </>
            )}

            <section style={{ marginTop: 24 }}>
              <h3>ÂÆüÊñΩË®òÈå≤</h3>
              <p>
                <strong>ÂÆüÊñΩÊó•Ôºö</strong> {record.practiceDate}
              </p>
              <p>
                <strong>ÊåØ„ÇäËøî„ÇäÔºö</strong>
              </p>
              <p>{record.reflection}</p>
              {record.boardImages.length > 0 && (
                <div style={{ marginTop: 12 }}>
                  <strong>ÊùøÊõ∏ÂÜôÁúüÔºö</strong>
                  <div style={{ display: "flex", gap: 8, flexWrap: "wrap", marginTop: 8 }}>
                    {record.boardImages.map((img, i) => (
                      <img
                        key={img.name + i}
                        src={img.src}
                        alt={img.name}
                        style={{
                          width: 160,
                          height: 160,
                          objectFit: "cover",
                          borderRadius: 4,
                          border: "1px solid #ccc",
                        }}
                      />
                    ))}
                  </div>
                </div>
              )}
            </section>
          </section>

          <div style={{ display: "flex", flexDirection: "column", gap: 8 }}>
            <button onClick={handleSaveAll} style={actionBtn}>
              üíæ ‰∏ÄÊã¨‰øùÂ≠òÔºà„É≠„Éº„Ç´„É´„ÉªFirestore„ÉªDriveÔºâ
            </button>
            <button onClick={handlePdfDownload} style={pdfBtn}>
              üìÑ PDF„Çí„ÉÄ„Ç¶„É≥„É≠„Éº„Éâ
            </button>
          </div>
        </>
      )}
    </main>
  );
}
